* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: "roboto", sans-serif;
    font-size: 16px;
}

header {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 .5rem;
    background-color: #7E8D85;
    background-image: linear-gradient(180deg, #233329, #7E8D85, #233329);
    padding-bottom: .05rem;
}

header span {
    font-weight: bold;
    letter-spacing: .06rem;
    text-align: center;
    font-size: 1.2rem;
}

.branding {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.brand {
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: .05rem;
    position: relative;
    top: -1px;
    cursor: default;
}

.logo {
    min-width: 50px;
    position: relative;
    top: 2px;
}

.computer-choice-btn {
    margin: 0 1rem;
}

header button {
    background: none;
    color: #A2E3C4;
    border: none;
    font-size: 1.3rem;
    font-weight: bold;
    cursor: pointer;
    padding: 0 .5rem;
    text-align: center;
}

header button:hover, .choice-selected {
    text-decoration: underline;
    color: #c1e3a2;
}

.main {
    min-height: 100vh;
    display: grid;
    grid-template-rows: min-content 1fr min-content;
    background-image: radial-gradient(black 50%, gray);
}

.game-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    margin: 3rem 2rem 2rem 2rem;
}

.board, .radar {
    display: grid;
    grid-template-columns: repeat(11, min-content);
    grid-template-rows: repeat(11, min-content);
    border: 1px solid #7E8D85;
    max-width: fit-content;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-color: rgb(18, 180, 180);
}

.radar {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: 90%;
    background-repeat: no-repeat;
    background-position: right bottom 12%;
    background-color: black;
    border-color: #00fe00;
}

.board > div, .radar > div {
    width: 50px;
    height: 50px;
    border: 1px solid #7E8D85;
    display: flex;
    position: relative;
}

.radar > div {
    border-color: #00fe00;
}

.board-coord-cell, .radar-coord-cell {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #c1e3a2;
    font-weight: bold;
    font-size: 1.5rem;
}

.radar-coord-cell {
    color: #c0ff85;
}

.board-cell-marker, .radar-cell-marker {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    z-index: 2;
    background-color: none;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    display: none;
}

div.board-cell-marker.hit, div.radar-cell-marker.hit {
    background-color: red;
    border: 2px solid rgb(219, 1, 1);
    position: absolute;
    display: block;
}

div.board-cell-marker.miss, div.radar-cell-marker.miss {
    background-color: white;
    border: 2px solid rgb(206, 206, 206);
    position: absolute;
    display: block;
}

.ship {
    width: 50px;
    background-color: none;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    cursor: pointer;
    z-index: 1;
}

.battleship {
    height: 200px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.carrier {
    height: 250px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
    background-size: 120% 100%;
}

.submarine {
    height: 150px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.destroyer {
    height: 150px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}

.patrol-boat {
    height: 100px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

.horizontal {
    flex-shrink: 0;
    height: 50px;
}

.horizontal.carrier {
    width: 250px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
    background-size: 100% 120%;
}

.horizontal.battleship {
    width: 200px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
}

.horizontal.destroyer {
    width: 150px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_11___});
}

.horizontal.submarine {
    width: 150px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_12___});
}

.horizontal.patrol-boat {
    width: 100px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_13___});
}

.ship-container {
    width: 292px;
    height: 292px;
    display: grid;
    grid-template-columns: repeat(5, min-content);
    align-items: center;
    background-color: #034078;
    background-image: radial-gradient(#7E8D85, black);
    border: 5px inset black;
    border-radius: 10px;
    padding: 1rem;
}

.interface button {
    font-size: 1.4rem;
    font-weight: bold;
    color: #c1e3a2;
    background-color: #7E8D85;
    padding: .3rem .6rem;
    border: 2px inset #7E8D85;
    border-radius: 10px;
    cursor: pointer;
    background-image: linear-gradient(180deg, #233329, #7E8D85, #233329);
}

.interface button:hover {
    background-image: linear-gradient(180deg, #7E8D85, #233329, #7E8D85);
    color: #c0ff85;
}

.interface {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}

.game-btns {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
}

.game-btns > div {
    display: flex;
    gap: 1.3rem;
    flex-wrap: wrap;
}

.hidden {
    display: none;
}


.popup {
    pointer-events: none;
    opacity: 0;
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
    width: fit-content;
    margin: 0 auto;
    z-index: 3;
    color: #9bff28;
    font-size: 5rem;
    font-weight: 900;
    animation-name: fade;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
}

.dialog {
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
    width: fit-content;
    margin: 0 auto;
    background-image: radial-gradient(#233329, #7E8D85);
    background-image: radial-gradient(#7E8D85, #233329);
    z-index: 4;
    padding: 1rem;
    border-radius: 10px;
    border: 4px outset #233329;
}

.dialog > ol {
    margin: 0 1rem 0 2.5rem;
}

.exit-btn-div {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.exit-btn-div > button {
    font-size: 1.2rem;
    font-family: "roboto", sans-serif;
    font-weight: bold;
    padding: .1rem .5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    border: 2px outset #233329;
    background-color: #233329;
    color: #c1e3a2;
}

.exit-btn-div > button:hover {
    background-color: #c1e3a2;
    color: #233329;
    border-color: #c1e3a2;
}

.exit-btn-div > button:active {
    border-style: inset;
}

.dialog  li {
    max-width: 45ch;
    color: #c1e3a2;
    margin: 1.5rem 0;
    font-size: 1.3rem;
    letter-spacing: .02rem;
}

@keyframes fade {
    0% {opacity: 1;}
    75% {opacity: 1;}
    100% {opacity: 0;}
